import React from 'react'
import Link from 'gatsby-link'
import Main from '../components/Main'
import Features from '../components/Features'
import About from '../components/About'
import Logos from '../components/Logos'

const logo = require('../assets/img/logos/1.svg')

const IndexPage = () => (
  <div>
    <Main
      tagline="Seu site personalizado em at 3 dias"
      subtagline="Ganhe presena na web com baixo custo e agilidade"
      cta="Adquira o seu"
      action={{
        href: 'http://dlbn.co/pt/contact'
      }}
    />
    <Features
      features={[
        {
          icon: 'smartphone',
          title: 'Conquiste seus usurios com um site veloz',
          description: '79% dos usurios de smartphones esperam obter informao imediata quando usam a internet'
        },
        {
          icon: 'search',
          title: 'Seja encontrado no Google',
          description: 'Pginas otimizadas para obter melhor posicionamento nos resultados de busca'
        },
        {
          icon: 'activity',
          title: 'Acompanhe seus resultados',
          description: 'Saiba a quantidade, perfil e comportamento de seus visitantes'
        },
        {
          icon: 'wind',
          title: 'Sem complicaes e esforo mnimo',
          description: 'Foque no seu negcio enquanto desenvolvemos a experincia do seu usurio'
        },
      ]}
    />
    <About content={{
      title: 'Sobre o Panthera',
      about: "Uma soluo para o **microempreendedor e o profissional liberal obterem presena na web**.\n\nO Panthera possibilita a **insero e expanso** daqueles que tem viso de crescimento empresarial num **mercado repleto de oportunidades** de negcios como o digital por um **preo muito acessvel e de forma prtica e rpida**.\n\nConsiste em um **site padro com itens personalizveis** que ser **entregue em at trs dias**. Sua nica tarefa ser **bater um papo com a gente** e nos contar o que deseja ter em seu site ;)"
    }} />
    <Logos title="Usado por" logos={[{
      src: logo,
      alt: 'adobe after effects',
      href: 'http://adobe.com'
    },{
      src: logo
    },{
      src: logo
    },{
      src: logo
    },{
      src: logo
    },{
      src: logo
    },{
      src: logo
    },{
      src: logo
    },]}/>
  </div>
)

export default IndexPage



